<template>
  <div class="container" ref="container">
    <div class="image-container" ref="imageContainer">
      <img alt="Vue logo" src="./assets/profile.jpg" class="image" ref="image" @click="requestOrientation">
    </div>
    <small v-if="isIOS">Click image</small>
    <h1>Jónas Grétar Jónasson</h1>
    <small>CV coming soon... (maybe)</small>
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data () {
    return {
      isIOS: false
    }
  },
  methods: {
    requestOrientation () {
      try {
        if (this.isIOS) {
					const image = this.$refs.image
					DeviceOrientationEvent.requestPermission()
            .then((response) => {
              if (response === "granted") {
                window.addEventListener("deviceorientation", evt => this.orientationHandler(evt, image), true)
              } else {
                alert("has to be allowed!")
              }
            })
            .catch(() => alert("not supported"))
        }
      } catch {
        console.log("iOS orientation request failed")
      }
    },
    orientationHandler (event, image) {
      const angleDelta = Math.sqrt(Math.pow(event.beta - 90, 2))

      if (angleDelta > 15) {
        const animationDuration = (15 / angleDelta).toFixed(2) + "s"
        image.style.animation = `spin ${animationDuration} linear infinite`
      } else {
        image.style.animation = ``
      }
    }
  },
  mounted () {
    this.isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent)

    // Request automatically if not iOS
    if (!this.isIOS) {
			const image = this.$refs.image
      window.addEventListener("deviceorientationabsolute", evt => this.orientationHandler(evt, image), true)
    }

    const container = this.$refs.container
    const imageContainer = this.$refs.imageContainer
    const image = this.$refs.image

    container.addEventListener("mousemove", (event) => {
      const { left, top, width, height } = imageContainer.getBoundingClientRect()
      const centerX = left + width / 2
      const centerY = top + height / 2
      const distance = Math.hypot(event.clientY - centerY, event.clientX - centerX)

      if (distance > 50) {
        const animationDuration = (100 / distance) + "s"
        image.style.animation = `spin ${animationDuration} linear infinite`
      } else {
        image.style.animation = ``
      }
    })

    container.addEventListener("mouseleave", () => {
      image.style.animation = "none"
    })
  }
}
</script>

<style scoped>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.spin {
  transform-origin: center center;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.image-container {
  align-items: center;
  position: relative;
  width: 200px;
  height: 200px;
  overflow: hidden;
}

.image {
  border-radius: 50%;
  height: 100%;
  width: 100%;
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

</style>
